/*some default colors*/
:root {
    --primaryCol: #01fba7;
    /*--primaryCol: #12EDC9;*/
    --secondaryCol: #4BD5E7CC;
    --backgroundCol: #212121;
    --fontCol: #DDDDDD;
    --linkCol: var(--secondaryCol);
    --emphasisCol: black;
    --opacityFactor: 0.75;
}

