* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: -apple-system, BlinkMacSystemFont, Verdana, 'Helvectica', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    color: var(--fontCol);
}

#top-view {
    display: flex;
    flex-direction: column;
    background-color: var(--backgroundCol);
    width: 100vw;
    height: 100vh;
    min-height: 100vh;
    max-width: 2560px;
    max-height: 100vh;
    padding: 1vw 1vw 0 1vw;
    overflow: hidden;
}

#main-div {
    overflow-y: auto;
    height: 100%;
}

body {
    background-color: var(--backgroundCol);
    /*background-color: black;*/
    color: var(--fontColor);
    display:flex;
    flex-direction: row;
    justify-content: center;
}
